import React, { useCallback, useMemo } from 'react'
import { Button, message as Message, Space } from 'antd'
import { useHistory } from 'react-router-dom'

function CartPayment() {
  const history = useHistory();

  const handlePay = useCallback(() => {
    return Message.error('     .')
  }, [])

  const handleNaverPay = useCallback(() => {
    return Message.error('   .')
  }, [])

  const handleKakaoPay = useCallback(() => {
    return Message.error('   .')
  }, [])

  const handleCancel = useCallback(() => {
    history.goBack()
  }, [])

  const spaceStyle = useMemo(() => ({ display: 'flex', justifyContent: 'center' }), [])

  return (
    <Space style={spaceStyle}>
      <Button type='primary' onClick={handlePay}></Button>
      <Button type='default' onClick={handleNaverPay}></Button>
      <Button type='ghost' onClick={handleKakaoPay}></Button>
      <Button type='ghost' onClick={handleCancel}></Button>
    </Space>
  )
}

export default CartPayment
