import React, { useCallback, useMemo } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import NumberFormat from 'react-number-format'
import { Table, Tag } from 'antd';
import { CloseOutlined } from '@ant-design/icons'

import { REMOVE_CART_ITEM_REQUEST } from '../../../reducers/types'
import { ProductAccessorySort, ProductClothesSort } from '../utils/ProductSort'

function CartDetails() {

  const dispatch = useDispatch();
  const { cartWithDetail, loadCartItemsLoading } = useSelector(state => state.user);

  const handleRemoveItem = useCallback((productId) => () => {
    dispatch({
      type: REMOVE_CART_ITEM_REQUEST,
      payload: {
        productId
      }
    })
  }, [])

  const getTagColor = useCallback((tag) => {
    if (ProductAccessorySort.includes(tag)) {
      return 'geekblue'
    }
    if (ProductClothesSort.includes(tag)) {
      return 'green'
    }
  }, [])

  const columns = [
    {
      title: '',
      dataIndex: 'image',
      key: 'image',
      render: (item) => (<img height={150} src={item} alt='product' />)
    },
    {
      title: '',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: '',
      dataIndex: 'quantity',
      key: 'quantity',
      render: quantity => `${quantity}`,
    },
    {
      title: '',
      dataIndex: 'price',
      key: 'price',
      render: price => <NumberFormat value={price} displayType={'text'} thousandSeparator={true} suffix={''} />,
    },
    {
      title: '',
      dataIndex: 'totalPrice',
      key: 'totalPrice',
      render: price => <NumberFormat value={price} displayType={'text'} thousandSeparator={true} suffix={''} />,
    },
    {
      title: 'Tags',
      key: 'tags',
      dataIndex: 'tags',
      render: tags => tags.map(tag => (
        <Tag color={getTagColor(tag)} key={tag}>{tag}</Tag>
      )),
    },
    {
      title: '',
      key: 'cart',
      render: (item) => <div onClick={handleRemoveItem(item.key)}><CloseOutlined /></div>
    },
  ]

  const onHeaderRowStyle = useMemo(() => (() => ({ align: 'center' })), [])
  const onRowStyle = useMemo(() => (() => ({ align: 'center' })), [])

  return (
    <Table
      columns={columns}
      dataSource={cartWithDetail}
      onHeaderRow={onHeaderRowStyle}
      onRow={onRowStyle}
      tableLayout='auto'
      loading={loadCartItemsLoading}
    />
  )
}

export default CartDetails
